    .off-canvas-sidebar nav .navbar-collapse,
    .sidebar {
        position: fixed;
        display: block;
        top: 0;
        height: 100vh;
        width: 260px;
        right: 0;
        left: auto;
        z-index: 1032;
        visibility: visible;
        background-color: #9a9a9a;
        overflow-y: visible;
        border-top: none;
        text-align: left;
        padding-right: 0;
        padding-left: 0;
        transform: translate3d(260px, 0, 0);
        transition: all .33s cubic-bezier(.685, .0473, .346, 1)
    }

    .off-canvas-sidebar nav .navbar-collapse>ul,
    .sidebar>ul {
        position: relative;
        z-index: 4;
        width: 100%
    }

    .off-canvas-sidebar nav .navbar-collapse:before,
    .sidebar:before {
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        position: absolute;
        background-color: #282828;
        display: block;
        content: "";
        z-index: 1
    }

