    .close-layer {
        height: 100%;
        width: 100%;
        position: absolute;
        opacity: 0;
        top: 0;
        left: auto;
        background: rgba(0, 0, 0, .35);
        content: "";
        z-index: 9999;
        overflow-x: hidden;
        transition: all .37s ease-in
    }

    .close-layer.visible {
        opacity: 1
    }

    .navbar-toggler .icon-bar {
        display: block;
        position: relative;
        background: #555 !important;
        width: 24px;
        height: 2px;
        border-radius: 1px;
        margin: 0 auto
    }

    .navbar-header .navbar-toggler {
        padding: 15px;
        margin-top: 4px;
        width: 40px;
        height: 40px
    }

    .bar1,
    .bar2,
    .bar3 {
        outline: 1px solid transparent
    }

    @keyframes j {
        0% {
            top: 0;
            transform: rotate(0deg)
        }

        45% {
            top: 6px;
            transform: rotate(145deg)
        }

        75% {
            transform: rotate(130deg)
        }

        to {
            transform: rotate(135deg)
        }
    }

    @keyframes k {
        0% {
            top: 6px;
            transform: rotate(135deg)
        }

        45% {
            transform: rotate(-10deg)
        }

        75% {
            transform: rotate(5deg)
        }

        to {
            top: 0;
            transform: rotate(0)
        }
    }

    @keyframes l {
        0% {
            bottom: 0;
            transform: rotate(0deg)
        }

        45% {
            bottom: 6px;
            transform: rotate(-145deg)
        }

        75% {
            transform: rotate(-130deg)
        }

        to {
            transform: rotate(-135deg)
        }
    }

    @keyframes m {
        0% {
            bottom: 6px;
            transform: rotate(-135deg)
        }

        45% {
            transform: rotate(10deg)
        }

        75% {
            transform: rotate(-5deg)
        }

        to {
            bottom: 0;
            transform: rotate(0)
        }
    }

    .navbar-toggler .icon-bar:nth-child(2) {
        top: 0;
        animation: k .5s 0s;
        animation-fill-mode: forwards
    }

    .navbar-toggler .icon-bar:nth-child(3) {
        opacity: 1
    }

    .navbar-toggler .icon-bar:nth-child(4) {
        bottom: 0;
        animation: m .5s 0s;
        animation-fill-mode: forwards
    }

    .navbar-toggler.toggled .icon-bar:nth-child(2) {
        top: 6px;
        animation: j .5s 0s;
        animation-fill-mode: forwards
    }

    .navbar-toggler.toggled .icon-bar:nth-child(3) {
        opacity: 0
    }

    .navbar-toggler.toggled .icon-bar:nth-child(4) {
        bottom: 6px;
        animation: l .5s 0s;
        animation-fill-mode: forwards
    }

